@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  text-decoration: none;
}
a:visited {
  color: inherit;
}
button {
  outline: none;
  border: none;
  background: none;
}

body {
  font-family: 'Press Start 2P', 'Neo둥근모 Code', cursive;
  color: #2c2c2c;
}

button {
  cursor: pointer;
}

button,
input {
  font-family: 'Press Start 2P', 'Neo둥근모 Code', cursive;
}

header {
  display: flex;
  align-items: center;
}

@keyframes fire {
  0% {
    color: rgb(248, 50, 50);
    transform: scale(1);
  }
  30% {
    color: rgb(253, 57, 43);
    transform: scale(1.1);
  }
  60% {
    color: rgb(247, 163, 37);
    transform: scale(0.9);
  }
  80% {
    color: rgb(243, 74, 22);
    transform: scale(1.1);
  }
  100% {
    color: rgb(248, 50, 50);
    transform: scale(1);
  }
}

header {
  padding: 1.5em;
}

.header-icon {
  font-size: 1.4rem;
  margin-bottom: 0.3em;
  color: rgb(248, 50, 50);
  animation: fire 3000ms infinite;
}

.title {
  font-size: 1.2rem;
  padding: 0 0.5em;
}

.total-count {
  display: block;
  font-size: 1rem;
  text-align: center;
  width: 32px;
  height: 32px;
  line-height: 32px;
  border-radius: 50%;
  color: #fff;
  background-color: rgb(250, 50, 50);
}

.add-form {
  display: flex;
  margin-left: 1.5em;
  margin-bottom: 1em;
}

.add-input {
  width: 300px;
  height: 25px;
  font-size: 1rem;
  font-weight: normal;
  text-indent: 0.4em;
}

.add-button {
  display: inline-block;
  font-size: 0.9rem;
  border: 1px solid #000;
  border-radius: 6px;
  margin-left: 0.5em;
  padding: 0 0.7em;
  transition: all 100ms ease-in;
}

.add-button:hover {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
}

.todo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-gap: 10px;
  gap: 10px;
  padding: 0.5em 2em;
  width: 500px;
}

.todo-name__wrap {
  display: flex;
}

.todo-name {
  display: inline-block;
  line-height: 35px;
  font-size: 1.5rem;
}

.todo-button {
  display: inline-block;
  font-size: 1.4rem;
  width: 35px;
  height: 35px;
  border: 1px solid #000;
  border-radius: 6px;
  transition: all 100ms ease-in;
  margin-right: 0.4em;
}

.todo-count {
  display: block;
  margin-left: 0.2em;
  width: 40px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  border-radius: 50%;
  background-color: rgb(218, 218, 218);
}

.todo-plus,
.todo-minus {
  color: rgb(88, 88, 88);
}

.todo-plus:hover,
.todo-minus:hover {
  background-color: rgb(88, 88, 88);
  color: rgb(255, 255, 255);
}

.todo-delete {
  color: rgb(255, 0, 0);
}

.todo-delete:hover {
  color: #fff;
  background-color: rgb(255, 0, 0);
}

.reset {
  color: #fff;
  background-color: red;
  padding: 0.4em 0.8em;
  font-size: 1rem;
  border-radius: 6px;
  margin: 0 0 2em 2em;
}

